import { makeAutoObservable } from 'mobx';

export class AnimalStore {
  constructor() {
    this._animals = [
      { id: 1, title: '' },
      { id: 2, title: '' },
      { id: 3, title: '' },
      { id: 4, title: '' },
      { id: 5, title: '' },
      { id: 6, title: '' },
    ];
    makeAutoObservable(this);
  }
  setAnimal(animal) {
    this._animals = animal;
  }
  get animal() {
    return this._animals;
  }
}
